.dmc-lockscreen {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  background-size: cover!important;

  background-color: #3f51b5;
  background-color: var(--mdc-theme-primary, #3f51b5);

  .dmc-lockscreen__sidebar {
    width: 400px;
    background: #fff;
    background: var(--mdc-theme-background, #fff);
    opacity: 0.95;

    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: auto;
  }

  .dmc-lockscreen__card {
    padding: 16px;
    margin-top: -130px;

   .dmc-lockscreen__headline {
      margin-bottom: 0px;
    }

    .dmc-lockscreen__subheading {
      margin-top: 0px;
      margin-bottom: 24px;
    }
  }

  .dmc-lockscreen__action {
    width: 100%;
  }

  .dmc-lockscreen__content {
    width: 100%;
    height: 100%;

    .dmc-lockscreen__icon {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;

      >i.material-icons {
        color: white;
        font-size: 20rem;
      }
    }
  }

  @media (max-width: 600px) {
    .dmc-lockscreen__card {
      margin-top: 0px;
    }
    .dmc-lockscreen__sidebar {
      width: 100%;
      justify-content: flex-start;
    }
    .dmc-lockscreen__content {
      display: none;
    }
  }

  @media (max-height: 400px) {
    .dmc-lockscreen__card {
      margin-top: 0px;
    }

    .dmc-lockscreen__content {
      .dmc-lockscreen__icon {
        >i.material-icons {
          font-size: 5rem;
        }
      }
    }
  }
}
